            <h1>Документация REST API</h1><h2>Вступление</h2><p>API интерфейс используется для того, чтобы интегрировать систему световой индикации в проект клиента. API предназначен для разработчиков и сопровождается детальной документацией.</p><h2>Описание</h2><p>REST API работает по протоколу HTTP и представляет собой набор методов, с помощью которых совершаются запросы и возвращаются ответы для каждой операции. Все ответы приходят в виде JSON структур.</p><h2>Основной URL</h2><p>Все ссылки на запросы к API в данной документации включают обязательный основной URL:</p><pre><code>http://{IP ADDRESS CONTROLLER}:3000/api/v1</code></pre><p>Где переменная <strong>{IP ADDRESS CONTROLLER} </strong>является IP адресом контроллера в Вашей локальной сети. Его можно увидеть на дисплее контроллера.</p><h2>Тип передачи данных</h2><p>Вы можете использовать такие типы передачи данных как: </p><pre><code>«application/x-www-form-urlencoded» </code></pre><pre><code>«application/json» </code></pre><p>Для этого установите тип в заголовке запроса:</p><pre><code>Content-Type: «application/x-www-form-urlencoded» </code></pre><p>или</p><pre><code>Content-Type: «application/json»</code></pre><p></p><h2>Методы</h2>
